.list-lnav {
	overflow: hidden;
  margin-bottom: -12px;

	li {
		display: inline-block;
		margin-bottom: 12px;
		margin-right: 24px;
		@include font(16,28,80,24,36,80);
    font-weight: 400;
		@include media(sp){
			@include vw(margin-bottom,24);
			@include vw(margin-right,24);
		}
	}
	&--center {
		text-align: center;
		li {
			margin-right: 18px;
			margin-left: 18px;
			@include media(sp){
				@include vw(margin-left,14);
				@include vw(margin-right,14);
			}
		}
	}
}

.list-faq {
	&__heading {
		@include font(18,32,80,32,56,80);
    font-weight: 400;
    margin-top: -5px;
		@include media(sp){
			// @include vw(margin-bottom,50);
			@include vw(margin-top,0);
		}
    position: relative;
    padding-bottom: 0.75em;
    &:after {
      content: '';
      position: absolute;
      height: 2px;
      bottom: 0;
      background-color: #f0f0f0;
      right: -32px;
      left: -32px;
      @include media(sp){
        @include vw(right,-54);
        @include vw(left,-54);
      }
    }
	}
	&__list {
		position: relative;
	}
	&__item {
		padding: 32px;
		@extend .text-m;
		border-bottom: 2px solid #f0f0f0;
		@include media(sp){
			@include vw(padding,56);
		}
    &:last-child{
      border-bottom: none;
      padding-bottom: 0;
      margin-bottom: -5px;
      @include media(sp){
        @include vw(margin-bottom,0);
      }
    }
	}
	&__Q {
		position: relative;
		padding-left: 36px;
		margin-bottom: 1em;
		font-weight: 700;
		@include media(sp){
			@include vw(padding-left,70);
		}
		&:before {
			content: 'Q';
			position: absolute;
			left: 0px;
			top: -0.5em;
			font-weight: normal;
			@extend .fontsize-lm;
			@include media(sp){}
		}
	}
	&__A {
		position: relative;
		padding-left: 36px;

		@include media(sp){
			@include vw(padding-left,70);
		}
		&:before {
			content: 'A';
			position: absolute;
			left: 0px;
			top: -0.5em;
			font-weight: normal;
			@extend .fontsize-lm;
			@include media(sp){}
		}
	}
}
