.business_hours-wrap {
  display: flex;
  width: 100%;
  justify-content: space-between;
  @include media(sp) {
    display: block;
  }
  // align-items: center;
}

.business_hours-box {
  position: relative;
  width: 445px;
  background-color: #fff;
  padding: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  @include media(sp) {
    width: auto;
    display: block;
    @include vw(padding,32,48);
    &:first-child {
      @include vw(margin-bottom,40);
    }
  }
  .business_hours-title {
    position: absolute;
    left: 32px;
    top: 28px;
    @include media(sp) {
      @include vw(left,48);
      @include vw(top,32);
    }
  }
  .business_hours-horizontal {
    display: none;
  }
  .business_hours-vertical {

  }
  // & > div {
  //   position: relative;
  //
  // }
}

.business_hours-box2 {
  background-color: #fff;
  padding: 32px 18px;
  text-align: center;
  width: 100%;
  @include media(sp) {
    @include vw(padding,32);
  }

  .business_hours-horizontal {
    display: block;
		@include media(sp) {
			display: none;
		}
  }

  .business_hours-vertical {
    display: none;
    @include media(sp) {
			display: block;
		}
  }
}

.business_hours-title {
  @include font(14,14,100,28,28,100);
}

// 営業時間
////////////////////////

.business_hours {

  &__table {
    // width: 100%;
    display: table;
    margin-top: 30px;
    // border-spacing: 1px;
    // border-collapse: separate;
    margin-left: auto;
    margin-right: auto;
    @include media(sp) {
      width: 100%;
      @include vw(margin-top,32);
    }

    @include parent('.business_hours-box'){
      @include media(sp) {
        @include vw(margin-top,56);
      }
    }


    th {
      background-color: #f0f0f0;
      text-align: center;
      min-width: 105px;
      border-bottom: 1px solid #fff;

      @include media(sp) {}
      @include parent('.business_hours-horizontal'){
        min-width: 108px;
        @include media(sp) {}
      }
    }

    tr:last-child {
      th {
        border: 1px solid #f0f0f0;

      }
    }


    @include parent('.business_hours-horizontal'){
      td {
        border: 1px solid #f0f0f0;
      }
    }
    td {
      border: 1px solid #f0f0f0;
    }
    td,
    th {
      padding: 8px 14px;
      @include font(14,24,100,28,56,100);
      vertical-align: middle;
      @include media(sp) {}
      @include parent('.business_hours-horizontal'){
        padding: 8px 0;
      }
    }
    th div {

    }
    td div {
    }

  }
  &__text {

    @include parent('.business_hours-box'){
      @include font(24,32,100,32,48,100);
      @include media(sp) {
        @include vw(padding-top,40);
      }
    }
    @include parent('.business_hours-box2'){
      @include font(18,18,100,36,36,100);
      margin-top: 22px;
      @include media(sp) {

      }
    }

  }
  &__text2 {
    @include font(14,32,100,24,48,100);
    display: inline-block;
  }

  &__note {
    margin-top: 20px;
    @include font(10,20,260,20,40,260);
    @include media(sp) {

    }
  }
  &__note2 {
    display: inline-block;
    @include font(10,20,100,20,40,100);
    @include media(sp) {

    }
  }


}

// 営業日カレンダー
////////////////////////

.business_day {
  margin-left: auto;
  margin-right: auto;
  &__table {
    text-align: center;
    @include media(sp) {
      width: 100%;
    }
    th {
      font-family: $font-en;
      color: #ccc;
      @include font(10,10,100,20,20,100);
      @include media(sp) {

      }
    }
    td {
      padding: 7px;
      @include font(14,14,100,24,24,100);
      @include media(sp) {
        @include vw(padding,5);
      }
      &.holiday {
        span{
          background-color: #f0f0f0;
          @include media(sp) {}
        }
      }
      &.non-active {
        color: #cccccc;
      }
      &.today {
        span {
          &:after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            border: 2px solid #000;
          }

        }
      }
      span {
        position: relative;
        display: inline-block;
        width: 32px;
        padding: 9px 0;
        @include media(sp) {}
      }
    }
  }
  &__legend {
    position: absolute;
    right: 30px;
    top: 30px;
    @include media(sp) {
      @include vw(right,48);
      @include vw(top,40);
    }
    span {
      @include font(10,10,100,20,20,100);
      display: inline-block;
      margin-left: 15px;
      padding-left: 21px;
      position: relative;
      &:before {
        content: '';
        display: block;
        height: 14px;
        width: 14px;
        box-sizing: border-box;
        position: absolute;
        left: 0;
        top: -2px;
        @include media(sp) {}
      }
    }
  }
  &__legend_holiday {
    &:before {
      background-color: #f0f0f0;
    }
  }
  &__legend_today {
    &:before {
      border: 2px solid #000;
    }
  }
  &__box {
    position: relative;
    margin-top: 50px;
    margin-bottom: 20px;
    text-align: center;
    @include font(16,16,100,28,28,100);
    @include media(sp) {}
  }
  &__button {
    position: absolute;
    top: 0;
    width: 8px;
    height: 14px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100%;
    cursor: pointer;
    @include media(sp) {}

    &.js-business_day-prev {
      background-image: url(../images/ico-arrow-prev-b.svg);
      left: 7px;
    }
    &.js-business_day-next {
      background-image: url(../images/ico-arrow-next-b.svg);
      right: 7px;
    }
  }

}
