#loading {
  z-index: 10000;
	opacity: 1;
	position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
	$selector: opacity transform;
	@include trans($selector, 0.5s, 0s,$easeInOutCubic);
  &__inner {
    position: absolute;
    width: 50px;
  	height: 50px;
  	border-radius: 50%;
  	top: 50%;
  	left: 50%;
  	animation: spin 0.75s linear infinite;
  	margin-left: -25px;
  	margin-top: -25px;
    border-top: 4px solid rgba(#000, 0.1);
    border-right: 4px solid rgba(#000, 0.1);
    border-bottom: 4px solid rgba(#000, 0.1);
    border-left: 4px solid #ccc;
  }
	&.hide {
		opacity: 0;
	}
}
