@charset "utf-8";
////////////////////////
// トップメイン

.top-main {
	max-height: 800px;
	min-width: 1064px;
	position: relative;
	&:after {
		content: '';
		display: block;
		padding-top: 55%;
	}
	@include media(sp) {
		min-width: 1px;
		@include vw(max-height,1000);
		&:after {
			content: '';
			display: block;
			padding-top: 100% * 1000/750;
		}
	}
	&__pic {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100vw * 594/1080;
		min-height: 594px;
		max-height: 800px;
		background-size: cover;
		background-position: center center;
		@include media(sp) {
			@include vw(height,1000);
			@include vw(max-height,1000);
		}
		@include parent('.top-main--center'){
			// height: 100vw * 594/1080;
			// min-height: 594px;
			// max-height: 800px;
			// @include media(sp) {
			// 	@include vw(height,1000);
			// 	@include vw(max-height,1000);
			// }
		}

		&:after {
			content: '';
			z-index: -1;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
		}
	}

	&__pic_cover {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: 0.15;
		background-color: #000;
	}
	// -------------
	&__header {
		z-index: 2;
		position: absolute;
		left: 0;
		top: 38px;
		@include media(sp) {
			@include vw(height,118);
		}
	}

	&__inner {
		display: flex;
		align-items: center;
		position: absolute;
		z-index: 3;
		right: 20px;
		top: 0;
		height: 100px;
		@include media(sp) {
			display: none;
		}
	}

	&__logo {
		color: #fff;
		a {
			color: #fff;
		}
		@include media(sp) {
			opacity: 0;
		}
	}

	&__info {
		position: relative;
		padding-left: 31px;
		height: 32px;
		@include media(sp) {
			background-color: transparent;
			padding-top: 0;
			padding-right: 0;
			@include vw(padding-left,56);
			margin-left: auto;
			margin-right: auto;
		}

		.ico-tel {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			background-image: url("../images/ico-tel.svg");
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 16px;
			width: 30px;
			height: 30px;
			background-color: transparent;
			margin-top: 1px;
			@include media(sp) {
				@include vw(width,102);
				@include vw(background-size,33);
				height: 100%;
				margin-top: 0;
				top: 0;
			}
		}
	}

	&__tel {
		position: relative;
		top: 50%;
		transform: translateY(-50%);
		@extend .letterspacing-m;
		@include font(20,20,100,40,40,100);
		a {
			color: #fff;
		}
		i {
			display: inline-block;
			@include font(24,24,0,40,40,100);
			transform: translate3d(-2px,2px,0);
		}
	}
	// -------------
	&__inner2 {
		position: relative;
		height: 100%;
		@include media(sp) {}
	}
	// キャッチコピー
	&__box {
		font-feature-settings : "palt";
		position: absolute;
		color: #fff;
		// left: 102px;
		left: 0;
		right: 0;
		bottom: 50%;
		transform: translateY(75%);

		@include parent('.top-main--center'){
			text-align: center;
			width: 100%;
			left: 0;
		}
		@include media(sp) {
			top: auto;
			text-align: left;
			left: 0;
			transform: translateY(0);
			@include vw(bottom,116);
			@include vw(padding-left,46);
			@include vw(padding-right,46);
		}

	}
	// &__text {
	// 	@include font(18,18,80,24,24,80);
	// 	font-weight: 400;
	// 	margin-bottom: 20px;
	// 	@include media(sp) {
	// 		@include vw(margin-bottom,20);
	// 	}
	// }

	&__catch {
		@include font(32,48,200,48,72,140);
		width: 80%;
		margin-left: auto;
		margin-right: auto;
		@include media(sp) {
			width: auto;
		}
		span {
			// margin-left: 102px;
			display: inline-block;
			position: relative;
			font-weight: 500;
		}
	}

	// -------------
	&__nav {
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: #fff;

		@include parent('.top-main--center'){
			right: 32px;
	    bottom: 10px;
			left: auto;
			background-color: transparent;
			.list-nav li a {
				color: #fff;
			}
		}

		@include media(sp) {
			display: none;
		}
	}

	.list-nav {
		text-align: center;
		@include media(sp) {}
		li {
			display: inline-block;
			a {
				font-weight: 400;
				display: block;
				@include font(14,14,100,30,30,80);
				color: #000;
				padding: 24px 12px;
				@include media(sp) {}

			}
		}
	}
}
