@charset "utf-8";

.button {
	margin-left: auto;
	margin-right: auto;
	width: 342px;
	// @extend .op;
	@include media(sp) {
		@include vw(width,420);
		margin-left: auto;
		margin-right: auto;
	}
	&--auto {
		max-width: 342px;
		width: auto;
		@include media(sp) {
			@include vw(width,420);
			margin-left: auto;
			margin-right: auto;
		}
	}
	&--left {
		margin-left: 0;
		margin-right: auto;
	}
	&--small {
		width: 214px;
		a {
			padding: 10px;
			@include media(sp) {
				@include vw(padding,20);
			}
		}
	}
	.bold {
		font-weight: 500;
	}
	a {
		display: block;
		text-align: center;
		@include font(14,14,260,24,24,260);
		font-weight: 500;
		padding: 18px;



		$selector: color background-color;
		@include trans($selector, 0.4s, 0s,$easeOutExpo);


		// background-color: $custom-bgcolor;
		// border: 2px solid $custom-border-color;
		// color: $custom-color;

		@include media(sp) {
			@include vw(padding,28);
		}
		// @include parent('.column--full') {
		// 	border: 2px solid $custom-border-color2;
		// 	color: $custom-color2;
		// 	background-color: $custom-bgcolor2;
		// }
	}
}
