@charset "utf-8";


.form {
  input[type=radio], input[type=checkbox] {
    display: none;
  }
  @include font(14,28,10,28,28,10);

  &__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    @include media(sp){
      display: block;
      @include vw(margin-bottom,60);
    }
    // align-items: center;
  }
  &__text {
    font-weight: 500;
    width: 170px;
    padding-top: 12px;
    @include media(sp){
      width: auto;
      padding-top: 0;
      @include vw(margin-bottom,30);
    }
  }
  &__form {
    width: 480px;
    @include media(sp){
      width: auto;
    }
  }
  &__confirm {
    @include font(12,24,100,28,46,80);
    text-align: center;
    margin-bottom: 40px;
    @include media(sp){
      @include vw(margin-bottom,80);
    }
  }

  .select,
  textarea,
  input[type=number],
  input[type=email],
  input[type=url],
  input[type=tel],
  input[type=tel],
  input[type=search],
  input[type=text],
  input[type=password] {
    appearance: none;
    border-radius: 0;
    box-shadow: none;
    box-sizing: border-box;
    width: 100%;
    padding: 8px 24px;
    border: 1px solid #e0e0e0;
    @include font(14,28,80,28,28,80);
    @include media(sp){
      @include vw(padding,30,60);
    }
    &::-webkit-input-placeholder {
      color: #dedede;
    }
  }
  .required {
    position: relative;
    // padding-right: 30px;
    @include media(sp){}
    &:after {
      content: '必須';
      // position: absolute;
      display: inline-block;
      margin-left: 6px;
      color: #fff;
      background-color: #c34040;
      // right: 0;
      // top: 12px;
      padding: 0px 6px ;
      @include font(12,12,100,24,24,80);
      @include media(sp){
        @include vw(padding,10,14);
      }
    }
  }
}

.list-form {
  overflow: hidden;
  padding-top: 8px;
  // margin-bottom: 10px;
  @include font(12,24,100,28,46,80);
  @include media(sp){
    padding-top: 0;
  }
  &__item {
    float: left;
    margin-right: 20px;
    margin-bottom: 14px;
    @include media(sp){
      @include vw(margin-right,32);
      @include vw(margin-bottom,30);
    }
  }
}

.radio, .checkbox {
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding-left: 38px;
  cursor: pointer;
  @include media(sp){
    @include vw(padding-left,64);
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 22px;
    height: 22px;
    background-color: #fff;
    border: 1px solid #e0e0e0;
    @include media(sp){
      @include vw(width,48);
      @include vw(height,48);
    }
  }
}

.checkbox {
  @include media(sp){}
  &:before {

    @include media(sp){}
    input[type=checkbox]:checked + & {
      background-position: center center;
      background-repeat: no-repeat;
      background-image: url(../images/ico-check.svg);
      background-size: 15px;
      @include media(sp){
        @include vw(background-size,31);
      }
    }
  }
}

.radio {

  @include media(sp){}
  &:before {
    border-radius: 50%;
    @include media(sp){}
    input[type=radio]:checked + & {
      background-position: center center;
      background-repeat: no-repeat;
      background-image: url(../images/ico-radio.svg);
      background-size: 10px;
      @include media(sp){
        @include vw(background-size,20);
      }
    }
  }
}


.select {
  -webkit-appearance: button;
  -moz-appearance: button;
  appearance: button;
  vertical-align: middle;
  width: 100%;
  height: 48px;
  padding: 10px 12px;
  border: 1px solid #e0e4e6;
  border-radius: 0;
  background-image: url(../images/ico-select.svg);
  right: center;
  background-size: 13px;
  background-position: 95% center;
  background-repeat: no-repeat;
  background-color: #fff;

  @include media(sp){
    @include vw(height,96);
    @include vw(background-size,24);

  }

}

.button-form {
  border: none;
  cursor: pointer;
  outline: none;
  appearance: none;
	display: block;
	text-align: center;
	@include font(14,14,260,24,24,260);
  font-weight: 500;
	padding: 0;
  color: #fff;
  width: 214px;
  height: 42px;
  font-weight: 500;
  margin-left: auto;
  margin-right: auto;
  background-color: #000;

  @include media(sp){
    @include vw(width,428);
    @include vw(height,96);
  }


}
