@charset "utf-8";

.feature {
  position: relative;
  overflow: hidden;

  @include media(sp) {
    @include vw(margin-bottom,80);

    &:last-child {
      margin-bottom: 0;
    }
  }
  &.reverse{
    display: none;
    @include parent('.pic-flex') {
      align-items: flex-start;
      @include media(sp){
        flex-direction: column-reverse;
        .feature__pic {
          @include vw(margin-top,40);
        }
      }
    }
    @include parent('.pic-flex2') {
      align-items: flex-start;
      @include media(sp){
        flex-direction: column-reverse;
        .feature__pic {
          @include vw(margin-top,40);
        }
      }
    }
  }
  @include parent('.column--full') {
    color: #fff;
    margin-bottom: 0;
  }
  @include parent('.pic-flex') {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @include media(sp) {
      //
      flex-wrap: wrap;
    }
  }
  @include parent('.pic-flex2') {
    display: flex;
    justify-content: space-between;
    // align-items: center;
    width: 100%;
    flex-direction: row-reverse;
    @include media(sp) {
      // flex-direction: column-reverse;
      flex-wrap: wrap;
    }

  }

  &__pic {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    // align-items: flex-start;

    @include media(sp) {
    }
    @include parent('.pic-normal') {
      position: relative;
      overflow: hidden;

      // &:after {
      //   content: '';
      //   display: block;
      //   padding-top: 100% * 293/416;
      //   overflow: hidden;
      // }
      // @include media(sp) {
      //   @include vw(margin-bottom,70);
      // }
      //
      // img {
      //   position: absolute;
      //   // opacity: 0;
      //   top: 0;
      //   left: 0;
      // }
    }
    @include parent('.pic-flex') {
      width: 560px;
      @include media(sp) {
        width: auto;
      }
      position: relative;
    }
    @include parent('.pic-flex2') {
      width: 560px;
      @include media(sp) {
        width: auto;
      }
      position: relative;
    }
    @include parent('.pic-full') {
      overflow: hidden;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;

    }
    @include parent('.pic-full2') {
      overflow: hidden;
      img {
        opacity: 0;
        @include media(sp) {
          opacity: 1;
        }
      }
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      margin-bottom: 30px;
      @include media(sp) {
        @include vw(height,528);
        @include vw(margin-bottom,70);
      }
    }
    @include parent('.column--full') {
      // filter: blur(20px);
      // transform: scale(1.1);
    }
    @include parent('.pic-flow') {
      margin-bottom: -30px;
      @include media(sp) {
        @include vw(margin-bottom,-50);
      }
    }

    &--img {
      margin-bottom: 40px;
      @include media(sp) {
        @include vw(margin-bottom,50);
      }
    }
  }

  &__box {
    position: relative;
    @include parent('.column--single') {
    }
    @include parent('.column--full') {
      padding-top: 90px;
      padding-bottom: 90px;
      @include media(sp) {
        @include vw(padding,80,0);
      }
    }
    @include parent('.column--two_full') {
      padding-top: 60px;
      padding-bottom: 60px;
      @include media(sp) {
        @include vw(padding,80,0);
      }
    }
    @include parent('.column--three_full') {
      padding-top: 60px;
      padding-bottom: 60px;
      @include media(sp) {
        @include vw(padding,80,0);
      }
    }
    @include parent('.column--four_full') {
      padding-top: 60px;
      padding-bottom: 60px;
      @include media(sp) {
        @include vw(padding,80,0);
      }
    }
    @include parent('.box-center') {
      margin-left: auto;
      margin-right: auto;
    }
    // @include parent('.column--three_full'){
    //   margin-left: auto;
    //   margin-right: auto;
    // }
    &--basic {
      width: 490px;
      @include media(sp) {
        width: auto;
      }
    }
    @include parent('.column--narrow') {
      width: 296px;
      @include media(sp) {
        width: 100%;
      }
    }
    &--narrow {
      @include media(sp) {
        width: auto;
      }
    }
    &--white {
      padding: 40px;
      background-color: #fff;
      @include media(sp) {
        @include vw(padding,48,0);
      }
      .text-lead {
        margin-bottom: 20px;
        @include media(sp) {
          @include vw(margin-bottom,20);
        }
      }
    }
    &.add-mt {
      margin-top: 100px;
      @include media(sp) {
        margin-top: 0;
      }
    }
  }

  &__flow {
    @include font(40,40,80,80,80,80);
    margin-bottom: 0.4em;
    @include parent('.pic-flex') {
      margin-top: 25px;
      @include media(sp) {
        display: none;
      }
    }
    @include parent('.pic-flex2') {
      margin-top: 25px;
      @include media(sp) {
        display: none;
      }
    }
  }
  &__flow2 {
    @include font(40,40,80,80,80,80);
    margin-bottom: 0.4em;
    @include parent('.pic-flex') {
      margin-top: 25px;
      @include media(sp) {
        margin-top: 0;
      }
    }
    @include parent('.pic-flex2') {
      margin-top: 25px;
      @include media(sp) {
        margin-top: 0;
      }
    }
  }

  &__lead {
    @include font(24,32,100,40,60,100);
  }
  &__lead2 {
    @include font(18,28,100,40,60,100);
  }

  &__divide {
    &:after {
      content: '';
      margin-top: 20px;
      margin-bottom: 30px;
      display: block;
      height: 2px;
      width: 16px;
      background-color: #000;
      @include media(sp) {
        @include vw(width,36);
        @include vw(margin-top,30);
        @include vw(margin-bottom,40);
      }
    }
    @include parent('.pic-flex2') {
      @include media(sp) {
        @include vw(margin-bottom,32);
        @include vw(margin-top,49);
      }
      &:after {
        @include media(sp) {
          display: none;
        }
      }
    }
    @include parent('.pic-flex') {
      @include media(sp) {
        @include vw(margin-bottom,32);
        @include vw(margin-top,49);
      }
      &:after {
        @include media(sp) {
          display: none;
        }
      }
    }
    &.tac-sp {
      &:after {
        @include media(sp) {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
  }

  &__divide2 {
    &:after {
      content: '';
      margin-top: 20px;
      margin-bottom: 20px;
      display: block;
      height: 2px;
      width: 16px;
      background-color: #000;
      margin-left: auto;
      margin-right: auto;

      @include media(sp) {
        @include vw(width,36);
        @include vw(margin-top,30);
        @include vw(margin-bottom,40);
      }
    }
  }

  &__heading {
    word-wrap: break-word;
    @include parent('.text-lv1') {
      @extend .heading;
      text-align: center;
    }
    @include parent('.text-lv2') {
      @extend .heading;
    }
    @include parent('.text-lv3') {
      @extend .heading2;
    }
    @include parent('.text-lv4') {
      @extend .heading2;
    }
    @include parent('.column--single') {
      font-weight: 300;
      @include font(56,56,80,64,64,80);
      margin-bottom: 30px;
      @include parent('.font-ja'){
        font-weight: 400;
        @include font(32,32,80,56,56,80);
        // @include font(24,24,80,36,36,80);
        // @include font(32,32,80,45,45,80);
      }
      @include media(sp) {
        @include vw(margin-bottom,32);
      }
    }
    @include parent('.column--two_full') {
      font-weight: 300;
      @include font(56,56,80,64,64,80);
      @include parent('.font-ja'){
        font-weight: 400;
        @include font(32,32,80,56,56,80);
        // @include font(24,24,80,36,36,80);
      }
      margin-bottom: 20px;
      @include media(sp) {
        @include vw(margin-bottom,32);
      }
    }
  }
  &__text {
    @extend .text-m;
  }
  &__name {
    @extend .text-ml;
    margin-bottom: 0.5em;
    @include media(sp) {}
  }
  &__button {
    @include parent('.column--single') {}
    @include parent('.pic-flex') {}
    @include parent('.pic-flex2') {}
  }
}

.color-cover {
  @include parent('.column--full') {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0.5;
    background-color: #000;
  }
}

.pic-flex,
.pic-flex2{
  margin-top: 52px;

  @include media(sp) {
    @include vw(margin-top,70);
  }
  &.add-mt {
    margin-top: 75px;
    @include media(sp) {
      @include vw(margin-top,100);
    }
  }
  &.mt-0 {
    margin-top: 0;
    @include media(sp) {
      margin-top: 0;
    }
  }

}
