@charset "utf-8";

.page-title {
  height: 200px;
  position: relative;
  @include media(sp){
    min-width: 1px;
    @include vw(height,320);
  }
  &__pic {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
    @include media(sp){}
  }
  &__pattern {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-image: url(../images/bg-title-cool.png);
  }

  &__cover {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.99;
  }
  &__cover2 {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #f0f0f0;
    opacity: 0.95;

  }
  &__inner {
    position: relative;
    height: 100%;
  }
  &__text {
    position: relative;
    color: #fff;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    font-weight: 400;
    @include font(32,32,100,48,48,100);
    @include parent('.font-ja'){
      @include font(24,24,100,48,48,100);
    }
  }
  &--pic {
    height: 240px;
    @include media(sp){
      min-width: 1px;
      @include vw(height,320);
    }
    .page-title__cover {
      background-color: #000;
      opacity: 0.5;
    }
  }
  &--basic {
    .page-title__cover {
      opacity: 0.95;
    }
  }
  &--simple {
    .page-title__text {
      color: #000;
    }
  }
  &--center {
    .page-title__text {
      text-align: center;
    }
  }
}
