@charset "utf-8";

.greeting {
  display: flex;
  justify-content: space-between;
  @include media(sp){
    display: block;
  }
  &__pic {
    width: 176px;
    @include media(sp){
      @include vw(width,408);
      margin-left: auto;
      margin-right: auto;
      @include vw(margin-bottom,64);
    }
  }
  &__text {
    width: 465px;
    @include media(sp){
      width: auto;
    }
  }
}

.divide-image {
  height: 368px;
  position: relative;
  @include media(sp){
    min-width: 1px;
    @include vw(height,508);
  }
  &__pic {
    // position: absolute;
    // left: 0;
    // top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
    @include media(sp){}
  }
  &__pic_cover {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.15;
  }
}
