@charset "utf-8";


.slit-image {
  position: relative;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  height: 240px;
  @include parent('.is-iframe'){
    background-attachment: scroll;
  }
  @include media(sp){
    @include vw(height,120);
  }
  &__cover {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    opacity: 0.5;
  }
}
