.news-headline-compact {
	display: flex;
	justify-content: space-between;
	position: relative;
	width: 848px;
	margin-left: auto;
	margin-right: auto;
	@include media(sp) {
		display: block;
		width: auto;
	}

	&__divide {
		position: absolute;
		width: 1px;
		top: 6px;
		bottom: 6px;
		left: 164px;
		background-color: #000;
		@include media(sp) {
			display: none;
		}
	}
	&__title {
		width: 126px;
		margin-top: 4px;
		text-align: right;
		@include media(sp) {
			text-align: left;
			width: auto;
			margin-top: 0;
			@include vw(margin-bottom,60);
		}
	}

	&__list {
		width: 642px;
		@include media(sp) {
			width: auto;
		}
		li {
			margin-top: 10px;
			&:first-child {
				margin-top: 0;
			}
			@include media(sp) {
				@include vw(margin-top,34);
			}
			a {
				display: block;
				@extend .motion-opacity;
				@include media(sp) {}
			}
			dl {
				display: flex;
				width: 100%;
				@include media(sp) {}
			}
			dt {
				@include font(14,24,80,24,36,80);
				// @extend .letterspacing-m;
				width: 642px - 540px;
				@include media(sp) {
					@include vw(width,180);
				}
				span {
					display: inline-block;
				}
			}
			dd {
				@include font(12,24,80,24,36,80);
				width: 540px;
				@include media(sp) {
					@include vw(width,440);
				}
			}
		}
	}

	&__link {
		position: absolute;
		left: 0;
		bottom: 0;
		// display: none;
		width: 124px;
		text-align: right;

		&.show {
			display: block;
		}
		// @extend .font--m;
		@include font(12,24,80,20,20,80);
		@include media(sp) {
			bottom: auto;
			top:0;
			left: auto;
			right: 0;
		}
	}

	.news-arrow {
		display: none;
	}
}
////////////////////////
// news-archive

.news-archive {
	margin-bottom: 40px;
	border-top: 2px solid #f0f0f0;
	@include media(sp) {
		@include vw(margin-bottom,60);
	}

	li {
		border-bottom: 2px solid #f0f0f0;

		a {
			@extend .motion-opacity;
			display: block;
			padding: 24px 80px 24px 32px;
			position: relative;
			$selector: background-color;
			@include trans($selector, 0.2s, 0s,$easeInOutCubic);

			@include media(sp) {
				@include vw(padding,50,140,56,56);
			}
			&:hover {
				background-color: #f9f9f9;
				.news-arrow {
					transform: translateX(3px);
				}
			}

			&:after {
				content: '';
				width: 10px;
				height: 3px;
				background-color: #f00;
			}
		}
	}

	dl {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		flex-direction: column-reverse;
	}

	dd {
		width: 100%;
		@extend .text-m;
		margin-bottom: 15px;
		@include media(sp) {
			@include vw(margin-bottom,36);
		}
	}

	dt {
		width: 100%;
		@extend .fontsize-m;
		@extend .letterspacing-m;

		span {
			display: inline-block;
			padding: 0;
			@include media(sp) {
				@include vw(padding,5,10);
			}
		}
	}
}

.link-pager {
	overflow: hidden;
	position: relative;
	height: 54px;
	@include media(sp) {
		height: auto;
		@include vw(padding-bottom,136);
	}
	&--narrow {
		@include media(sp) {
			padding-bottom: 0;
			@include vw(height,88);
		}
	}


	&__item {
		width: 120px;
		@include media(sp) {
			@include vw(width,300);
		}

		&--prev {
			margin-left: 32px;
			float: left;
			@include media(sp) {
				@include vw(margin-left,56);
			}

			a {
				text-align: right;

				&:before {

					left: 21px;
					background-image: url("../images/ico-arrow-prev-b.svg");
					@include media(sp) {
						@include vw(left,20);
					}
				}

				&:hover {
					&:before {
						transform: translateX(-3px);
						background-image: url("../images/ico-arrow-prev.svg");
					}
				}

			}
		}

		&--next {
			margin-right: 32px;
			float: right;
			@include media(sp) {
				@include vw(margin-right,56);
			}
			a {
				text-align: left;

				&:before {
					right: 21px;
					background-image: url("../images/ico-arrow-next-b.svg");
					@include media(sp) {
						@include vw(right,20);
					}
				}
				&:hover {
					&:before {
						transform: translateX(3px);
						background-image: url("../images/ico-arrow-next.svg");
					}
				}

			}
		}

		&--index {
			width: 165px;
			position: absolute;
			left: 50%;
			bottom: 0;
			transform: translateX(-50%);
			@include media(sp) {
				@include vw(width,632);
			}

			a {
				// text-align: left;
			}
		}

		a {
			position: relative;
			padding: 18px;
			display: block;
			@include media(sp) {
				@include vw(padding,28);
			}

			&:before {
				content: '';
				width: 7px;
				height: 11px;
				background-size: cover;
				background-repeat: no-repeat;
				position: absolute;
				$selector: transform;
				@include trans($selector, 0.3s, 0s,$easeInOutCubic);

				@include media(sp) {
					@include vw(width,15);
					@include vw(height,23);
				}
			}
		}
	}
}
////////////////////////
// news-detail

.news-detail {
	margin-bottom: 40px;
	@include media(sp) {
		@include vw(margin-left,56);
		@include vw(margin-right,56);
		@include vw(margin-bottom,60);
	}

	&__title {
    @include font(18,32,80,40,66,80);
		margin-left: 32px;
		margin-right: 32px;
    font-weight: 500;

		@include media(sp) {
			// @include vw(margin-bottom,24);
			margin-left: 0;
			margin-right: 0;
		}
	}

	&__box {
		margin-top: 40px;
		@extend .text-m2;
		position: relative;
		padding-top: 36px;
		padding-bottom: 12px;
		@include media(sp) {
			@include vw(margin-top,56);
		}

		&:after,
		&:before {
			content: '';
			position: absolute;
			height: 2px;
			background-color: #f0f0f0;
			right: 0;
			left: 0;
			@include media(sp) {
				@include vw(right,-56);
				@include vw(left,-56);
			}
		}

		&:before {
			top: 0;
		}

		&:after {
			bottom: 0;
		}

		p {
			margin-left: 32px;
			margin-right: 32px;
			margin-bottom: 2.4em;
			@include media(sp) {
				margin-left: 0;
				margin-right: 0;
			}
		}
	}

	&__pic {
		margin-bottom: 2em;
		@include media(sp) {
			/* @include vw(padding,34); */
		}
	}

	&__data {
		margin-left: 32px;
		@include media(sp) {
			margin-left: 0;
		}
		@extend .fontsize-m;
		@extend .letterspacing-m;
    margin-bottom: 1em;
		span {
			display: inline-block;
			padding: 0;
			@include media(sp) {
				@include vw(padding,5,10);
			}
		}
	}
}
////////////////////////
// news-arrow

.news-arrow {
	width: 24px;
	height: 24px;
	position: absolute;
	right: 32px;
	top: 50%;
	margin-top: -12px;
	background-color: transparent;
	border-radius: 50%;

	$selector: transform;
	@include trans($selector, 0.2s, 0s,$easeInOutCubic);


	@include media(sp) {
		@include vw(width,48);
		@include vw(height,48);
		@include vw(right,60);
	}

	&:before {
		content: '';
		position: absolute;
		top: 48%;
		right: 35%;
		transform: rotate(45deg);
		transform-origin: bottom right;
		background-color: #000;
		@include media(sp) {
			// transform: translate3d(100%,0,0);
		}
	}

	&:after {
		content: '';
		position: absolute;
		top: 48%;
		right: 35%;
		transform-origin: top right;
		transform: rotate(-45deg);
		background-color: #000;
		@include media(sp) {}
	}

	&:after,
	&:before {
		width: 10px;
		height: 2px;
		@include media(sp) {
			@include vw(width,20);
			@include vw(height,4);
		}
	}
}
