@charset "utf-8";

.list {
  @extend .text-m;
  li {
    position: relative;
    &:before {

    }
  }
  &--basic {
    li {
      padding-left: 2em;
      &:before {
        content: '・';
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

  &--decimal {
    li {
      padding-left: 1em;
      margin-left: 2em;
      list-style-type: decimal-leading-zero;
    }
  }
}

.list-image {

  &__list {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    @include media(sp){
      display: block;
      @include vw(margin-top,96);
    }
    &:first-child {
      margin-top: 0;
    }
  }
  &__pic {
    width: 232px;
    @include media(sp){
      width: auto;
      @include vw(padding-bottom,48);
    }
  }
  &__box {
    // @extend .text-lm;
    width: 408px;
    @include media(sp){
      width: auto;
    }
  }
  &__heading {
    @extend .text-lead2;
    &:after {
      margin-top: 1em;
      content: '';
      display: block;
      width: 24px;
      height: 2px;
      background-color: #000;
      @include media(sp){
        @include vw(width,48);
      }
    }

  }
  &__text {
    @extend .text-m;
  }


}

.list-map {

  @include media(sp){
    @include vw(width,632);
    margin-left: auto;
    margin-right: auto;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    margin-top: 32px;
    min-height: 210px;
    @include media(sp){
      display: block;
      // @include vw(padding-bottom,60);
      @include vw(margin-top,96);
    }
    &:first-child {
      margin-top: 0;
    }
  }
  &__pic {
    width: 296px;
    @include parent('.box-sub') {
      width: 215px;
    }
    @include media(sp){
      @include vw(width,632);
      @include vw(height,300);
      margin-left: auto;
      margin-right: auto;
      // @include vw(padding-bottom,48);
    }
  }
  &__box {
    width: 368px;
    padding: 30px 40px;
    @include parent('.box-sub') {
      width: auto;
    }
    @include media(sp){
      width: auto;
      @include vw(padding,60);
    }

  }
  &__heading {
    @extend .text-lead2;
    font-weight: 500;
  }
  &__text {
    @extend .text-m;
  }
}

.list-link {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  width: 936px;
  margin-bottom: -18px;
  &--1 {
    width: 312px;
  }
  &--2 {
    width: 624px;
  }
  @include media(sp){
    display: block;
    // justify-content: space-between;
    @include vw(width,632);
    @include vw(margin-bottom,-20);
  }
  &__item {
    width: 288px;
    height: 80px;
    margin-left: 12px;
    margin-right: 12px;
    margin-bottom: 18px;

    @include media(sp){
      @include vw(margin-bottom,20);
      @include vw(width,576);
      @include vw(height,160);
      margin-left: auto;
      margin-right: auto;
    }
    a {
      @include font(16,28,80,32,32,80);
      display: block;
      position: relative;
      text-align: center;
      height: 100%;
      background-color: #fff;
      span {
        display: inline-block;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
      }
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border: 1px solid #f0f0f0;
      }
    }
  }
}
