@charset "utf-8";

.common-sns {
  display: flex;
  align-items: center;
  @include parent('.sp-menu') {
    justify-content: center;
    @include vw(margin-bottom,60);
  }
  @include parent('.block--sns') {
    justify-content: center;
  }
  @include parent('.top-main') {
    margin-left: 30px;
    li {
      a {
        color: #fff;
      }
    }
  }
  &__ico {
    display: inline-block;
    width: 32px;
    margin-right: 15px;
    @include media(sp) {
      margin-right: 0;
      @include vw(width,60);
      // @include vw(height,60);

      @include parent('.sp-menu') {
        @include vw(width,45);
      }


      margin-right: 0;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__text {
    // vertical-align: middle;
    display: inline-block;
    font-weight: 500;
    @include font(14,24,80,24,36,80);
    @include media(sp) {
      display: none;
    }
  }

  li {
    margin-right: 18px;
    margin-left: 18px;
    text-align: center;

    @include media(sp) {

      @include vw(margin-left,25);
      @include vw(margin-right,25);

    }

    a {
      display: flex;
      align-items: center;
      height: 100%;
      background-repeat: no-repeat;
      background-position: center center;
      // vertical-align: middle;
      @include media(sp) {
        display: flex;

      }
    }
  }
}
