@charset "utf-8";

.main {
	position: relative;
	z-index: 1;
  background-color: #fff;
  // padding: 1px;
}

.block {

  &--sub-top{
    padding-top: 72px;
    @include media(sp){
      @include vw(padding-top,120);
    }
  }
	&--broad{
		padding: 90px 0;
		@include media(sp){
			@include vw(padding,90,0);
		}
	}
	&--medium{
		padding: 62px 0;
		@include media(sp){
			@include vw(padding,60,0);
		}
	}
	&--basic{
		padding: 80px 0;
		@include media(sp){
			@include vw(padding,80,0);
		}
	}
	&--basic2{
		padding: 80px 0;
		@include media(sp){
			@include vw(padding,80,0,112,0);
		}
	}
	&--basic3{
		padding: 80px 0;
		@include media(sp){
			@include vw(padding,100,0);
		}
	}
  &--basic_t{
    padding-top: 80px;
    @include media(sp){
      @include vw(padding-top,80);
    }
  }
	&--basic_b{
		padding-bottom: 80px;
		@include media(sp){
			@include vw(padding-bottom,120);
		}
	}
	&--basic_b2{
		padding-bottom: 80px;
		@include media(sp){
			@include vw(padding-bottom,80);
		}
	}
	&--narrow {
		padding: 40px 0;
		@include media(sp){
			@include vw(padding,40,0);
		}
	}
	&--narrow2 {
		padding: 40px 0;
		@include media(sp){
			@include vw(padding,64,0);
		}
	}
	&--narrow3 {
		padding: 32px 0;
		@include media(sp){
			@include vw(padding,32,0);
		}
	}
  &--narrow_b{
    padding-bottom: 40px;
    @include media(sp){
      @include vw(padding-bottom,40,0);
    }
  }
  &--narrow_t{
    padding-top: 40px;
    @include media(sp){
      @include vw(padding-top,40,0);
    }
  }
  &--narrow_lnav{
    padding-top: 26px;
    padding-bottom: 26px;
    @include media(sp){
      @include vw(padding,40);

    }
  }
	&--sns {
		padding-top: 32px;
    padding-bottom: 32px;
    @include media(sp){
      @include vw(padding-top,30,0);
    }
	}

}

.bg-color {
	background-color: #f0f0f0;
}

.divide{
  font-size: 0;
  line-height: 0;
  height: 2px;
  background-color: #f0f0f0;
}

.box {
	position: relative;
	width: 914px + 40px;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	@include media(sp){
		@include vw(width,632);
		padding-left: 0;
		padding-right: 0;
	}
	&--narrow {
		width: 736px + 40px;
		@include media(sp){
      width: auto;
			// @include vw(width,632);
		}
	}
	&--wide{
		padding: 0;
	}
}

.box2 {
	position: relative;
	// z-index: 1;
	width: 712px;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;

	@include media(sp){
		@include vw(width,632);
		padding-left: 0;
		padding-right: 0;
	}

}

.box-sub {
  display: flex;
  @include media(sp){
    display: block;
  }
  &__l {
    width: 175px;
    margin-right: 88px;
    @include media(sp){
      width: auto;
      margin-right: 0;
    }
  }
  &__r {
    width: 562px;
    @include media(sp){
      width: auto;
    }
  }
}

.box-others {
	border: 4px solid #fff;
	padding: 60px;
	margin-top: 75px;
	@include media(sp){
		@include vw(margin-top,100);
		@include vw(padding,54);
	}
}

.content {
  margin-left: 34px;
  margin-right: 34px;
	@include media(sp){
		@include vw(margin-left,54);
		@include vw(margin-right,54);
	}
	&--full{
    margin-left: 0;
    margin-right: 0;
		@include media(sp){
			@include vw(margin-left,0);
			@include vw(margin-right,0);
		}
	}
}

.column {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	@include media(sp){
		display: block;
	}
	&__item {
		// @include media(sp){
		// 	@include vw(margin-bottom,100);
		// 	&:last-child {
		// 		margin-bottom: 0;
		// 	}
		// }
	}
	&--single {
		display: block;
	}
	&--two_full {
		width: 100%;
		// border-bottom: 1px solid #f1f1f1;
		.column__item {
			width: 50%;
			position: relative;
			// padding-bottom: 64px;
			&:after {
				z-index: -1;
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 1px;
				height: 100%;
				background-color: #f1f1f1;
			}
		}
		.feature__box {
			// width: 100% * 416/640;
      @include media(sp){
        width: auto;
      }
			// width: px;
		}
	}
	&--two {
		width: 945px;
		margin-left: auto;
		margin-right: auto;
		.column__item {
			width: 416px;
		}
	}

	&--three_full {
		width: 100%;
		// border-bottom: 1px solid #f1f1f1;
		.column__item {
			width: calc(100% / 3);
			position: relative;
			// padding-bottom: 64px;
			&:after {
				z-index: -1;
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 1px;
				height: 100%;
				background-color: #f1f1f1;
			}
		}
		.feature__box {
			// width: 296px;
			// width: 100% * 296/428;
		}
	}
	&--three {
		.column__item {
			width: 240px;
			@include media(sp){
				width: auto;
			}
		}
	}
	&--four_full {
		width: 100%;
		// border-bottom: 1px solid #f1f1f1;
		.column__item {
			width: 25%;
			position: relative;
			// padding-bottom: 64px;
			&:after {
				z-index: -1;
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 1px;
				height: 100%;
				background-color: #f1f1f1;
			}
		}
		.feature__box {
			width: 100% * 184/320;
      @include media(sp){
        width: auto;
      }
			// min-width: 184px;
		}
	}
	&--four {
		.column__item {
			width: 232px;
		}
	}
	&--two ,
	&--three ,
	&--four {
		@include media(sp){
			@include vw(margin-bottom,0);
			// width: 945px;
			margin-left: auto;
			margin-right: auto;
			@include vw(width,632);
			.column__item {
				width: auto;
				@include vw(margin-top,88);
				margin-bottom: 0;
				&:first-child {
					@include vw(margin-top,68);
				}
			}
			.feature__text {
				@include vw(width,510);
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
  &--full {
		@include media(sp){
      .column__item {
        width: auto;
      }
    }
  }
}
