@charset "utf-8";

.table {
  width: 100%;
  @extend .text-m;
  @include media(sp){
    display: block;
  }


  tbody {
    @include media(sp){
      display: block;
    }
  }
  tr {
    &:nth-child(odd){
      background-color: #f0f0f0;
    }
    @include media(sp){
      display: block;
    }
  }
  th {
    padding: 16px 32px 16px 32px;
    width: 143px;
    white-space: nowrap;

    box-sizing: border-box;
    text-align: left;
    span {
      font-weight: 500;
    }
    @include media(sp){
      @include vw(padding,32,54,0,54);
      display: block;
      width: 100%;
    }

  }
  td {
    padding: 16px 32px 16px 0;
    @include media(sp){
      @include vw(padding,12,54,32,54);
      display: block;
    }
    dl {
      &:nth-child(even){
        border-top: 2px solid #f4f4f4;
        padding-top: 20px;
        margin-top: 20px;
      }
      dt {
        font-weight: 500;
      }
    }

  }

}
