@charset "utf-8";

.page-top .header {
  position: fixed;
  transform: translate3d(0,0,0);
  background-color: transparent;

  $selector: transform background-color;
  @include trans($selector, 0.2s, 0s,$easeOutCubic);
  .header__logo a,
  .list-nav li a{
    color: #fff;
  }

  &.show {
    box-shadow: 0 2px 2px 2px rgba(0,0,0,0.04);
    transform: translate3d(0,0,0);
    background-color: #fff;
    .header__logo a,
    .list-nav li a{
      color: #000;
    }
    @include media(sp) {
      position: fixed;
      .button-menu span {
        background-color: #000;
      }
    }
  }

  &.hide {
    transform: translate3d(0,-100%,0);
    @include media(sp) {
      position: fixed;

    }
  }
  @include media(sp) {
    position: fixed;
    transform: translate3d(0,0,0);
    background-color: transparent;
    .button-menu span {
      background-color: #fff;
    }
    .header__logo a {
      $selector: color;
      @include trans($selector, 0.4s, 0s,$easeOutCubic);
      color: #fff;
    }
  }
}

.page-sub .header {
  box-shadow: 0 2px 2px 2px rgba(0,0,0,0.04);
  position: fixed;
  // transform: translate3d(0,-100%,0);
  // transform: translate3d(0,-100%,0);
  $selector: transform background-color;
  @include trans($selector, 0.2s, 0s,$easeOutCubic);

  &.show {
    box-shadow: 0 2px 2px 2px rgba(0,0,0,0.04);
    transform: translate3d(0,0,0);
    @include media(sp) {
      position: fixed;
    }
  }

  &.hide {
    transform: translate3d(0,-110%,0);
    @include media(sp) {
      position: fixed;
      transform: translate3d(0,-110%,0);
    }
  }
  @include media(sp) {
    // position: absolute;
    transform: translate3d(0,0,0);
  }
}

.header {
  z-index: 10;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: 1080px;
  height: 72px;
  // display: flex;
  // justify-content: space-between;
  // align-items: center;
  @include media(sp) {
    min-width: 1px;
    @include vw(height,120);
  }
  background-color: #fff;

  &__inner {
    justify-content: space-between;
    display: flex;
    align-items: center;
    // position: absolute;
    position: relative;
    height: 72px;
    @include media(sp) {
      @include vw(height,120);
    }
  }

  &__inner2 {
    height: 100%;
    display: flex;
    align-items: center;
    margin-right: 38px;
  }

  &__logo {}

  &__nav {
    right: 290px;
    top: 22px;
    width: auto;

    @include media(sp) {
      display: none;
    }
  }

  .btn-menu {
    position: absolute;
    right: 0;
    top: 0;
  }

  .list-nav {
    text-align: center;
    li {
      display: inline-block;
      @include media(sp) {}
      a {
        display: block;
        @include font(14,14,100,30,30,80);
        @include media(sp) {}
        font-weight: 500;
        padding: 12px;
      }
    }
  }
}

.button-menu {
  @include media(sp) {
    z-index: 10;
    @include vw(width,120);
    @include vw(height,120);
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;

    span {
      $selector: background-color;
      @include trans($selector, 0.4s, 0s,$easeOutCubic);
      position: absolute;
      top: 50%;
      @include vw(width,36);
      @include vw(height,4);
      @include vw(left,42);
      background-color: #000;

      &:nth-child(1) {
        @include vw(top,40);
      }

      &:nth-child(2) {
        @include vw(top,56);
      }

      &:nth-child(3) {
        @include vw(top,72);
      }
    }
  }
}

.button-menu-close {
  z-index: 10;
  @include media(sp) {
    @include vw(width,120);
    @include vw(height,120);
    position: fixed;
    right: 0;
    top: 0;
    cursor: pointer;
    span {
      position: absolute;
      top: 50%;
      @include vw(width,46);
      @include vw(height,3);
      @include vw(left,38);
      @include vw(top,56);
      background-color: #000;
      &:nth-child(1) {
        transform: rotate(45deg);
      }
      &:nth-child(2) {
        transform: rotate(-45deg);
      }
      background-color: #000;
    }
  }
}

.logo {
  margin-left: 52px;
  font-feature-settings : "palt";
  @include media(sp) {
    @include vw(margin-left,46);
  }
  @include font(16,16,150,32,32,150);
  color: #fff;

  a {
    font-weight: 700;
  }
}
