@charset "utf-8";

.btn-pagetop {
  position: absolute;
  z-index: 1;
  width: 42px;
  height: 42px;
  bottom: 0;
  right: 0;

  @include media(sp) {
    bottom: auto;

    width: 100%;
    @include vw(top,-100);
    @include vw(height,80);
    @include vw(right,0);
  }

  a {
    display: block;
    height: 100%;
    text-align: center;
    background-image: url("../images/ico-pagetop.svg");
    background-size: 18px;
    background-repeat: no-repeat;
    background-position: center;
    // background-color: #fff;
    @include media(sp) {
      @include vw(background-size,34);
    }
    $selector: transform;
    @include trans($selector, 0.3s, 0s,$easeInOutCubic);
    
    &:hover {
      transform: translateY(-3px);
    }
  }
}

.footer {

  &__top {
    position: relative;
    z-index: 1;
    padding-top: 68px;
    padding-bottom: 70px;
    background-color: #3d3d3d;
    color: #fff;
    @include media(sp) {
      @include vw(padding,106,0);
    }
    a {
      color: #fff;
    }
  }

  &__bottom {
    position: relative;
    z-index: 1;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: #fff;
    @include media(sp) {
      @include vw(padding,80,0);
    }
  }

  &__inner {
    // display: flex;
    // justify-content: space-between;
    // flex-wrap: wrap;
    // width: 100%;
    // max-width: 1020px;
    // margin-left: auto;
    // margin-right: auto;
    // @include media(sp) {
    //   display: block;
    //   @include vw(width,632);
    // }
  }

  &__companyname {
    width: 100%;
    font-feature-settings : "palt";
    @include font(16,16,150,32,32,150);
    font-weight: 500;
    margin-bottom: 8px;
    @include media(sp) {
      @include vw(margin-bottom,20);
    }
  }

  &__logo {

    text-align: center;
    margin-bottom: 10px;
    @include media(sp) {
      @include vw(margin-bottom,20);
    }
    img {
      height: 40px;
      width: auto;
      @include media(sp) {
        @include vw(height,60);
      }
    }
  }


  &__nav {
    // @include fontsize(14,32);
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -22px;
    width: 720px;
    @include media(sp) {
      width: auto;
    }
    li {
      width: 180px;
      margin-bottom: 12px;
      @include font(14,24,100,24,24,100);
      a {
        font-weight: 400 !important;
      }
      @include media(sp) {
        @include vw(margin-bottom,50);
        width: 50%;
        a {
          display: inline-block;
          // @include vw(padding-left,36);
          // @include vw(background-size,15);
          // background-image: url("../images/ico-arrow-next.svg");
          // background-repeat: no-repeat;
          // background-position: center left;
        }
      }
    }
  }
  &__text1 {
    @extend .text-s;
    margin-bottom: 20px;
    &:after {
      content: '';
      margin-top: 16px;
      margin-bottom: 36px;
      display: block;
      height: 2px;
      width: 24px;
      background-color: #fff;
      @include media(sp) {
        @include vw(width,50);
        @include vw(margin-top,40);
        @include vw(margin-bottom,72);
      }
    }
    span {
      display: inline-block;
    }
  }

  &__copyright {
    @extend .text-s;
    text-align: center;

  }

  &__language {}
}

.common-info {
  border: 1px solid #000;
  padding: 24px 32px;
  margin-bottom: 20px;
  margin-top: 10px;
  @include media(sp) {
    @include vw(margin-bottom,77);
    @include vw(margin-top,0);
  }
  @include parent('.sp-menu') {
    border: 1px solid #fff;
  }

  &__text1 {}

  &__text2 {
    @extend .text-m;
  }

  &__text3 {
    @extend .text-ml;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }
}

.common-language {
  width: 320px;
  position: relative;
  order: 4;
  background-color: #fff;
  @include parent('.sp-menu') {
    background-color: rgba(#fff,0.15);

    &:after {
      background-image: url("../images/ico-arrow-select.svg");
    }

    select {
      color: #fff;
    }
  }
  @include media(sp) {
    width: auto;
    border-radius: 0;
    @include vw(margin-bottom,60);
  }

  &:after {
    content: '';
    position: absolute;
    right: 16px;
    top: 50%;
    background-image: url("../images/ico-arrow-select-b.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    width: 7px;
    height: 4px;
    margin-top: -2px;
    @include media(sp) {
      @include vw(width,20);
      @include vw(height,12);
      @include vw(margin-top,-6);
      @include vw(right,33);
    }
  }

  span {
    position: absolute;
    left: 26px;
    top: 50%;
    transform: translateY(-50%);
    @extend .text-s;
    @include media(sp) {
      @include vw(left,40);
    }
  }

  select {
    position: relative;
    z-index: 1;
    -webkit-appearance: button;
    -moz-appearance: button;
    appearance: button;
    padding: 10px 12px 10px 100px;
    border-radius: 0;
    background-color: transparent;
    border: none;
    width: 100%;
    @extend .text-s;
    @include media(sp) {
      @include vw(padding,20,40,20,220);
    }
  }
}
