@charset "utf-8";

////////////////////////
// SP MENU

.sp-menu {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  z-index: 100;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(#fff, 0.95);
  text-align: center;

  -ms-overflow-style:none;
  &::-webkit-scrollbar{
      display:none;
  }

  &:after {
    content: '';
    display: inline-block;
    height: 100vh;
    vertical-align: middle;
  }

  a {
    // color: #fff;
  }

  &__logo {
    // @include font(20,20,100,32,32,100);
    // @include vw(margin-bottom,112);
    font-weight: 500;
    @include media(sp){

    }
  }
  &__inner {
    display: none;
  	@include media(sp){
      display: inline-block;
      vertical-align: middle;
      position: relative;
      z-index: 1;
  		// @include vw(padding-top,48);
  		// @include vw(width,630);
      margin-left: auto;
      margin-right: auto;
  	}
  }
  &__nav {
    text-align: center;
    li {
      @include font(18,18,0,36,36,100);
    	@include media(sp){
    		@include vw(margin-bottom,90);
    	}
      a {
        // font-weight: 500;
      }
    }
  }


}
