.fontsize-ls{ @include fontsize(24,40); }
.text-ls{ @include font(24,40,80,32,56,80); }


.fontsize-lm{ @include fontsize(24,48); }
.text-lm{ @include font(28,40,80,32,56,80); }

.fontsize-ll{ @include fontsize(32,48); }
.text-ll{ @include font(32,40,80,48,30,80); }

.letterspacing-s { @include letterspacing(80,80); }
.letterspacing-m { @include letterspacing(100,100); }
.letterspacing-l { @include letterspacing(80,80); }

.heading {
  // font-family: $font-en;
  font-weight: 300;
  @include font(56,56,80,80,80,80);
  @include parent('.font-ja'){
    font-weight: 400;
    @include font(32,32,80,56,56,80);
  }
  &--posfix {
    margin-top: -60px;
    @include media(sp){
      @include vw(margin-top,-95);
    }
  }
  &__divide {
    &:after {
      content: '';
      margin-top: 24px;
      margin-bottom: 35px;
      display: block;
      height: 2px;
      width: 24px;
      margin-left: auto;
      margin-right: auto;
      background-color: #000;
      @include media(sp) {
        @include vw(width,50);
        @include vw(margin-top,40);
        @include vw(margin-bottom,72);
      }
      @include parent('.column--full') {
        background-color: #fff;
      }
    }
    &--narrow {
      &:after {
        @include media(sp) {
          @include vw(margin-bottom,64);
        }
      }
    }
  }
}

.heading2 {
  // font-family: $font-en;
  font-weight: 300;
  @include font(40,40,80,48,48,80);
  @include parent('.font-ja'){
    font-weight: 400;
    @include font(18,18,80,26,26,80);
  }
  &--posfix {
    margin-top: -60px;
    @include media(sp){
      @include vw(margin-top,-95);
    }
  }

  @include parent('.column--full') {
    @include font(40,40,80,32,32,80);
  }
  @include parent('.news-headline-compact') {
    @include font(40,40,80,32,32,80);

    @include parent('.font-ja'){
      font-weight: 400;
      @include font(18,18,80,26,26,80);
      // @include font(24,24,80,36,36,80);
    }
    @include media(sp) {
      font-weight: 400;
    }
  }
}

.heading2_2 {
  font-weight: 400;
  @include font(32,32,80,56,56,80);
}


.heading-sub {
  font-weight: 400;
  @include font(18,18,160,36,36,160);
  text-align: center;

  &:after {
    content: '';
    margin-top: 24px;
    margin-bottom: 30px;
    display: block;
    height: 2px;
    width: 16px;
    margin-left: auto;
    margin-right: auto;
    background-color: #000;
    @include media(sp) {
      @include vw(width,36);
      @include vw(margin-top,30);
      @include vw(margin-bottom,40);
    }
  }
  @include parent('.box-sub__l') {

    text-align: right;
    @include media(sp) {
      text-align: center;
    }
    &:after {
      margin-bottom: 0;
      margin-left: auto;
      margin-right: 0.16em;
      @include media(sp) {
        margin-left: auto;
        margin-right: auto;
        @include vw(margin-top,30);
        @include vw(margin-bottom,40);
      }
    }
  }
}


.text-lead {
  margin-bottom: 1em;
  @extend .text-ls;
  &--center{
    text-align: center;
    @include media(sp){
      text-align: left;
    }
  }
}

.text-lead2 {
  @include font(16,28,80,32,56,80);
  margin-bottom: 1em;
}

.text-lead-sub {
  @include font(18,32,160,32,32,160);
  font-weight: 500;
  margin-top: -0.25em;
}
