@charset "utf-8";

.cards {
  &__item {
    position: relative;
    margin-bottom: 24px;
    @include media(sp){
      @include vw(margin-bottom,48);
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__inner {
    position: relative;
    padding: 36px 48px 43px;
    background-color: #fff;
    @include media(sp){
      @include vw(padding,36,48,43);
    }
  }
  &__heading {
    @extend .text-ml;
    font-weight: normal;
  }

  &__text {
    @extend .text-m;
  }
}
