.history {
	padding: 32px;
	@include media(sp){
		@include vw(padding,56);
		@include vw(width,632);
    margin-left: auto;
    margin-right: auto;
	}
	&__list {
		position: relative;
	}
	&__item {
		position: relative;
		padding-left: 120px;
		padding-bottom: 25px;
		@extend .text-mm;
		@include media(sp){
			@include vw(padding-left,200);
			@include vw(padding-bottom,40);
		}
		&:before {
			content: '';
			position: absolute;
			left: 91px;
			top: 8px;
			width: 2px;
			height: 100%;
			@include media(sp){
				@include vw(left,147);
				@include vw(top,16);
			}
		}
		&:last-child{
			padding-bottom: 0;
			&:before {
				display: none;
			}
		}
	}
	&__time {
		position: absolute;
		left: 0;
		top: 0;
		&:before {
			content: '';
			position: absolute;
			left: 88px;
			top: 8px;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			@include media(sp){
				@include vw(left,142);
				@include vw(top,16);
				@include vw(width,14);
				@include vw(height,14);
			}
		}
	}
	&__txt {

	}
}
