@charset "utf-8";
///////////////////////////////////////////
// generic.scss
///////////////////////////////////////////

html {
	font-size: 62.5%; /*10px*/
}

body {
	// @include font($base-font-size,$base-line-height);

	// color: $color-default;
	// background: $body-background;
	width: 100%;
	// font-feature-settings : "palt";

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	// min-width: 1000px;
	// max-width: 100%;
	
	height: 100%;
	min-width: 1080px;

	@include media(sp){
		min-width: 320px;
	}
}


input {
	font-family: $font-jp;
}

a {
	text-decoration: none;
}

a.link-sp {
  pointer-events: none;
  @include media(sp){
    pointer-events: auto;
  }
}

img {
	width: 100%;
	height: auto;
}

.underline {
	cursor: pointer;
	text-decoration: underline;
	&:hover{
		text-decoration: none;
	}
}
.blank {
	display: inline-block;
	padding-right: 17px;
	background-image: url(../images/ico-blank.svg);
	background-position: right center;
	background-repeat: no-repeat;
	background-size: 10px;
}

.blank-bnr {
	display: block;
  &:after {
    content: '';
    display: block;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 10px;
    height: 10px;
  	background-image: url(../images/ico-blank.svg);
  	background-position: right center;
  	background-repeat: no-repeat;
  	background-size: 10px;

  }
}


