@charset "utf-8";
////////////////////////
// access

.access {

	&__map {
		width: 100%;
		height: 240px;
		@include media(sp) {
			@include vw(margin-bottom,80);
			@include vw(width,750);
			@include vw(height,360);
		}
	}

  &__heading {
    &:after {
      display: none;
    }
    @include media(sp) {
      text-align: center;
      &:after {
        display: block;;
      }
    }
  }

	&__box {
		display: flex;
		justify-content: space-between;
		// align-items: center;
		@include media(sp) {
			display: block;
		}
	}

	&__name {
		font-weight: 400;
		@extend .text-ml;
		margin-bottom: 0.5em;
		@include media(sp) {}
		// @extend .letterspacing-m;
	}

	&__text {
		@extend .text-m;
		@include media(sp) {}
	}

}
